.weather-card {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 30px;
  border-radius: 20px;
  max-width: 400px;
  margin: 20px auto;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.weather-card h2 {
  font-size: 32px;
  margin: 0 0 20px 0;
  text-align: center;
}

.weather-card p {
  font-size: 18px;
  margin: 10px 0;
  line-height: 1.6;
}

.temperature {
  font-size: 48px;
  font-weight: bold;
  text-align: center;
  margin: 20px 0;
}

.weather-info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
  margin-top: 20px;
}

.info-item {
  background: rgba(255, 255, 255, 0.2);
  padding: 15px;
  border-radius: 10px;
  text-align: center;
}

.info-label {
  font-size: 14px;
  opacity: 0.8;
  margin-bottom: 5px;
}

.info-value {
  font-size: 20px;
  font-weight: bold;
}